*[v-cloak] {
	display: none; }

button, input, select {
	font-family: inherit; }

.rating {

	&__list {
		display: inline-flex;
		margin-right: 16px; }

	&__start {
		@include size(8px 16px);
		background: url("../img/ico-star.png") right top no-repeat;

		&:nth-child(odd) {
			background-position: left top; }

		&:nth-child(odd):not(:first-child) {
			padding-left: 4px;
			width: 12px;
			background-position: left 4px top; }

		&.active {
			background: url("../img/ico-star-active.png") top right no-repeat;

			&:nth-child(odd) {
				background-position: left top; }

			&:nth-child(odd):not(:first-child) {
				// padding-left: 4px
				// width: 12px
				background-position: left 4px top; } } }

	&__count {
		display: inline-block;
		vertical-align: top; } }

.rub {
	font-style: normal;
	font-size: inherit;
	font-weight: inherit;
	font-family: "Roboto"; }

.slick {

	&-slide {
		height: auto; }

	&-arrow {
		border: none;
		padding: 0;
		display: block; }

	&-list, &-track {
		min-width: 100%; } }

.head {
