		color: #fff;
		font-weight: bold;
		margin-right: 20px; }

	&__wrap {
		display: flex;
		align-items: center;
		justify-content: space-between; }

	&__left, &__right {
		width: persent(770, 1800); }

	&__left, &__right {
		display: flex;
		align-items: center; }

	&__right {
		justify-content: space-between;
		padding-left: 50px; }

	&__logo {
		flex: none;
		margin-right: 30px; } }

.head-contacts {

	&__link {
		color: #000;
		text-decoration: none; } }

.head-links {
	display: flex;
	justify-content: flex-end;

	&__link {
		// +size(32px)
		padding-left: 48px;
		color: #fff;
		text-decoration: none;
		height: 27px;
		line-height: 26px;
		white-space: nowrap;

		&:not(:first-child) {
			margin-left: 70px; }

		&--cart {
			position: relative;
			background: url("../img/ico-head-cart.png") center left no-repeat;

			&[data-count] {

				&:after {
					content: attr(data-count);
					border-radius: 50%;
					position: absolute;
					color: #fff;
					text-decoration: none;
					background: $main-color;
					@include size(20px);
					text-align: center;
					font-size: 11px;
					line-height: 20px;
					top: -6px;
					left: 17px;
					text-indent: 0 !important; } } }

		&--lc {
			background: url("../img/ico-lk.svg") center left no-repeat !important; } } }

.menu {
	display: flex;
	justify-content: center;
	position: relative;
	z-index: 7;

	&__link {
		font-weight: bold;
		text-transform: uppercase;
		color: #fff;
		text-decoration: none;
		position: relative;
		white-space: nowrap;

		&:after {
			content: "";
			position: absolute;
			left: 0;
			background: $main-color;
			top: calc(100% + 5px);
			@include size(100% 2px);
			transition: transform .3s ease;
			transform: scaleX(0); }

		&:hover, &:focus, &.active {

			&:after {
				transform: scaleX(1); } } }

	&__el {
		// padding-bottom: 55px

		&:not(:last-child) {
			margin-right: vw(35); }

		&.sub {

			div a {
				font-weight: 300; }

			.menu__link {
				display: block;
				padding-right: 21px;
				position: relative;
				z-index: 1;
				// font-weight: 300

				&:after, &:before {
					content: "";
					position: absolute; }

				&:after {
					right: 0;
					@include size(7px 4px);
					background: url("../img/ico-sub-arrow.png") no-repeat;
					top: 7px; }

				&:before {
					@include size(23px 12px);
					background: url("../img/ico-triangle.png") no-repeat;
					left: calc(50% - 12px);
					bottom: -55px;
					transition: opacity .4s ease;
					opacity: 0; } }

			&:hover {

				.submenu {
					opacity: 1;
					pointer-events: all; }

				.menu__link {

					&:before {
						opacity: 1; } } } } } }

